import merge from './../utils/merge';

function getMediaQuery(bp) {
	return `@media screen and (min-width: ${bp}px)`;
}

export let breakpoints = [480, 765, 960, 1200, 1600];

export const aliases = ['xs', 'sm', 'md', 'lg', 'xl', 'xxl'];

function createResponsive(overrides) {
	breakpoints = [...breakpoints, ...overrides];

	return {
		breakpoints,
		mediaQueries: breakpoints.map(getMediaQuery),
	};
}

export default createResponsive;



